/**
 * Hold the context keys used in the vsan overview tab by PersistentStorageService.
 * The context is required because in the overview page, the details view is opened inline w/o navigation.
 */
export class InlineViewContextKey {
   static readonly HEALTH_CHECK_CONTEXT_KEY: string = "__health_check_context__";
   static readonly SERVICES_CONTEXT_KEY: string = "__services_context__";
   static readonly FAULT_DOMAINS_CONTEXT_KEY: string = "__fault_domains_context__";
   static readonly RESYNCING_OBJECTS_CONTEXT_KEY: string = "__resyncing_objects_context__";
}
